@import url("https://fonts.googleapis.com/css2?family=Open+Sans&display=swap");

* {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
}

body {
  height: 100vh;
  width: 100%;
  overflow: hidden;
  background: linear-gradient(90deg, #e3ffe7 0%, #d9e7ff 100%);
} 

.App {
  display: flex;
  height: 100vh;
  width: 100%;
  align-items: center;
  justify-content: center;
}

.cont {
  width: 45%;
  height: 95%;
  margin: 20px;
  background: #f7fafc;
  outline: none;
  border: 1px solid #ddd;
  display: flex;
  flex-direction: column;
}

.nav {
  width: 100%;
  height: 40px;
  border-bottom: 1px solid #ddd;
  display: flex;
  background: #fff;
  align-items: center;
  font-size: 15px;
}

.MuiSvgIcon-root {
  padding: 10px;
  color: #888;
}

textarea {
  padding: 12px;
  border: none;
  outline: none !important;
  /*width: 96%;*/
  height: 100%;
  overflow: hidden;
  font-size: 18px;
  resize: none;
  background: #f7fafc;
}

.markdown {
  padding: 12px;
  border: none;
  outline: none !important;
  /*width: 96%;*/
  height: 100%;
  overflow-x: hidden;
  resize: none;
  background: #fff;
}
